@import url('https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@400;600;700&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
:root{
    --blue:#003;
    --red:#ff3946;
}
/* basic setup */
html{
    font-size: 62.5%;
    font-family: 'Akaya Telivigala', cursive!important;
}
body{
    font-family: 'Yanone Kaffeesatz', sans-serif;
    color: var(--blue);
    height: 100vh;
    display: flex;
    flex-direction: column;
}


.btn-get-started{
    text-decoration: none;
    font-size: 2rem;
    color: var(--blue);
    padding: 1rem 2rem;
    border-radius: 3rem;
    border: 2px solid #003;
    transition: 0.5s ease;
    display: inline-block;
    

}
.btn-get-started:hover{
    background-color: var(--blue);
    color: #fff;
}
p{
    font-size: 1.5rem;
}
h5{
    font-size: 1.8rem;
}
a{
    font-size: 1.4rem;
}
/* designing navbar */
.Navbar{
    background-color: #fff!important;
}
.active_menu{
    
    border-bottom: 0.1rem solid #ff3946;
}
.nav-link {
    font-size: 2rem;
    margin-right: 1.5rem;
    color: #003 !important;
    transition: 0.5s ease;
}
.active_menu:hover{
    color: var(--red)!important;
}

.navbar-brand {
    font-size: 2.5rem;
    color: var(--red);
    font-weight: 900;
}
/* designing heder section */
.my_brand{
    color: var(--red);
}
/* .placeit{
    margin-top: 10rem;
} */
#header{
    width: 100%;
    height: 80vh;
    margin-top: 35px;
}
.animated{
    text-align: right;
}
.animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
/* designing services page */
.card-img-top{
    height: 20rem;
}
.View_more{
  padding: 1rem 2rem;
  /* background-color: #000033!important; */
  font-size: 2rem;
  color: #003!important;
  border: 0.1rem solid#003!important;
  background: transparent!important;
    border-radius: 30rem;
    transition: 0.5s ease;
}
.View_more:hover{
    background: #003!important;
    color: white!important;
}
.click{
    font-size: 1.3rem!important;
}
/* ////////Contact ////////////
 */

 .contact_div {
    width: 100%;
    height: 75vh;
  }
  footer{
      margin: 0;
  }
@keyframes up-down{
  0%{
      transform: translateY(10px);
  }
  100%{
      transform: translateY(-10px);
  }
}
/* ////////////////responsive mediaquery code */
@media (max-width: 991px) {
    #header {
      height: 80vh;
      text-align: center;
    }
  
    #header .header-img {
      text-align: center;
    }
    #header .header-img img {
      width: 60%;
    }
  }
  
  @media (max-width: 768px) {
    #header {
      margin-top: 20px;
    }
    #header h1 {
      font-size: 28px;
    }
    #header h2 {
      font-size: 18px;
      margin-bottom: 30px;
    }
    #header .header-img img {
      width: 70%;
    }
  }